import { create } from "zustand";

export interface ConsoleLog {
  type: "log" | "error";
  args: any[];
}
interface ConsoleState {
  logs: ConsoleLog[];
  addLog: (_log: ConsoleLog) => void;
  clear: () => void;
}

const useConsoleStore = create<ConsoleState>((set) => ({
  logs: [],
  addLog: (log: ConsoleLog) => set((state) => ({ logs: [...state.logs, log] })),
  clear: () => set({ logs: [] }),
}));

// Override console.log to store logs
const originalConsoleLog = console.log;
console.log = (...args) => {
  originalConsoleLog.apply(console, args);
  useConsoleStore.getState().addLog({
    type: "log",
    args,
  });
};

const originalConsoleError = console.error;
console.error = (...args) => {
  originalConsoleError.apply(console, args);
  useConsoleStore.getState().addLog({
    type: "error",
    args: args,
  });
};

export default useConsoleStore;
