import React from "react";
import SettingForm from "../components/SettingForm";

const SettingsPage: React.FC = () => {
  const [advanceSettings, setAdvanceSettings] = React.useState(false);

  return (
    <div className="relative">
      <div className="p-3 pt-5">
        <h1 className="text-lg text-[18px]">Settings</h1>
        <p className="text-xs pt-1">Configure your Arbor Chat settings</p>
      </div>
      <SettingForm advanceSettings={advanceSettings} />
      <div
        className="absolute top-0 left-0 bg-transparent bg-opacity-80 text-white w-[20px] h-[20px]"
        onClick={() => setAdvanceSettings(!advanceSettings)}
      />
    </div>
  );
};

export default SettingsPage;
