import React, { useState } from "react";
import { UseFormRegister } from "react-hook-form";

interface TokenInputProps {
  id: string;
  label?: string;
  register: UseFormRegister<any>;
  placeholder?: string;
  className?: string;
}

export const TokenInput: React.FC<TokenInputProps> = ({ id, label, register, placeholder, className }) => {
  const [showToken, setShowToken] = useState(false);

  return (
    <div className="">
      {label && (
        <label htmlFor={id} className="items-center font-title text-sm mb-2">
          {label}
        </label>
      )}
      <div className="relative">
        <input
          className={`placeholder-color-lightGrey w-full rounded border border-lightGrey p-3 pr-14 ${className}`}
          type={showToken ? "text" : "password"}
          id={id}
          {...register(id)}
          placeholder={placeholder}
        />
        <button
          type="button"
          className="text-xs absolute right-4 top-1/2 -translate-y-1/2 text-royalBlue"
          onClick={() => setShowToken(!showToken)}
        >
          {showToken ? "Hide" : "Show"}
        </button>
      </div>
    </div>
  );
};
