import React, { useContext, useEffect, useState } from "react";
import { getFromLocalStorage } from "../taskpane";
import QuotaBar from "../components/QuotaBar";
import Instructions from "../components/Instructions";
import ConsoleView from "../components/ConsoleView";
import { isVersionSupported } from "../../functions/utils";
import { MIN_VERSION_SUPPORTED } from "../../constants";
import FormulaInUsed from "../components/FormulaInUsed";
import HelperSelection from "../components/HelperSelection";
import { useNavigate } from "react-router-dom";
import { DebugContext } from "../context";
// import store from "../../stores/store";

const Main = () => {
  const { isDebug, setDebug } = useContext(DebugContext);
  const [showVersionWarning, setShowVersionWarning] = useState(false);
  const [diagnostic, setDiagnostic] = useState(null);
  const [apiKey, setApiKey] = useState("");
  const navigate = useNavigate();
  const [apiHost, setApiHost] = useState("");

  useEffect(() => {
    const localApiKey = getFromLocalStorage("apiKey");
    const localApiHost = getFromLocalStorage("apiHost");
    setApiKey(localApiKey);
    setApiHost(localApiHost);
    if (!localApiKey) {
      navigate("/onboarding");
    }
    const diagnostics = Office.context.diagnostics;
    setDiagnostic(diagnostics);
    setShowVersionWarning(
      !isVersionSupported(
        diagnostics?.host as unknown as string,
        diagnostics?.platform as unknown as string,
        diagnostics?.version
      )
    );
  }, []);

  return (
    <div className="relative">
      <FormulaInUsed />
      <div className="w-full h-[1px] bg-lightGrey"></div>
      {showVersionWarning && (
        <div className="text-xs text-red p-3">
          {`Your version of Office is not supported. Functionality may be limited. Please upgrade to ${MIN_VERSION_SUPPORTED[diagnostic?.platform]} or later for full functionality. Current version: ${diagnostic?.version}`}
        </div>
      )}
      {/* <HelperSelection /> */}
      <div className="w-full h-[1px] bg-lightGrey"></div>
      <div className="p-3 pt-4">
        <h1 className="text-lg text-[18px]">Token Limitation</h1>
        <p className="text-xs pt-1">Tokens that are being use to generate ArborGPT functions</p>
      </div>
      <QuotaBar apiHost={apiHost} token={apiKey} />
      <div className="w-full h-[1px] bg-lightGrey"></div>
      <div className="p-3 pt-5">
        <h1 className="text-lg text-[18px]">ArborGPT Functions</h1>
        <p className="text-xs pt-1">Available formulas that can be used in your Excel</p>
      </div>
      <Instructions />
      {isDebug && diagnostic && (
        <div className="text-xs text-gray-500 mb-4 px-3">
          <p>Version: {diagnostic.version}</p>
          <p>Platform: {diagnostic.platform}</p>
        </div>
      )}
      {isDebug && <ConsoleView onClose={() => setDebug(false)} />}
      <div className="absolute top-0 left-0 w-[30px] h-[30px]" onClick={() => setDebug(!isDebug)} />
    </div>
  );
};

export default Main;
