import React from "react";
import { UseFormRegister } from "react-hook-form";

interface ModelProviderProps {
  register: UseFormRegister<any>;
  hide: boolean;
}

export const ModelProviderSelect = ({ register, hide }: ModelProviderProps) => {
  if (hide) {
    return null;
  }
  return (
    <div className="md:block duration-200 whitespace-nowrap rounded-lg font-title text-sm transition-colors my-4">
      <label className="items-center font-title text-sm mb-2">Model Provider</label>
      <div className="flex gap-4 py-3">
        <label className="flex items-center gap-2 px-1">
          <input type="radio" value="openai" {...register("modelProvider")} />
          OpenAI
        </label>
        <label className="flex items-center gap-2">
          <input type="radio" value="groq" {...register("modelProvider")} />
          Groq
        </label>
      </div>
    </div>
  );
};
