import React, { useContext, useState } from "react";
import { Link } from "react-router-dom";
import { DebugContext } from "../context";

const Layout = ({ children }: { children?: React.ReactNode }) => {
  const [showMenu, setShowMenu] = useState(false);
  const { isDebug, setDebug } = useContext(DebugContext);

  return (
    <>
      <header className="flex justify-center items-center p-3 bg-dark relative z-50 h-[48px] absolute top-0 left-0 right-0 w-full">
        <a className="flex items-center" href="https://www.arborchat.ai" target="_blank" rel="noopener noreferrer">
          <img src="/assets/logo-white.png" alt="logo" width={73} height={24} />
        </a>
        <div className="flex justify-end absolute right-3">
          <button className="text-white flex items-center" onClick={() => setShowMenu(!showMenu)}>
            {showMenu ? (
              <img src="/assets/close.png" width={24} height={24} alt="close" />
            ) : (
              <img src="/assets/menu.png" width={24} height={24} alt="menu" />
            )}
          </button>
        </div>
      </header>
      {/* Side Menu */}
      {showMenu && (
        <div className="fixed top-0 left-0 w-full h-full bg-transparent z-10" onClick={() => setShowMenu(false)}></div>
      )}
      <div
        className={`fixed top-0 right-0 h-full w-[260px] bg-dark transform transition-transform z-20 duration-300 ease-in-out ${
          showMenu ? "translate-x-0" : "translate-x-full"
        }`}
      >
        <div className="pt-[48px]">
          <div className="p-8 text-white">
            <nav className="flex flex-col gap-4">
              <Link to="/" className="text-sm" onClick={() => setShowMenu(false)}>
                Home
              </Link>
              <Link to="/settings" className="text-sm" onClick={() => setShowMenu(false)}>
                Settings
              </Link>
              <a href="https://www.arborchat.ai/pro" target="_blank" rel="noopener noreferrer" className="text-sm">
                Pricing
              </a>
            </nav>
          </div>
          <div className="w-full h-[1px] bg-darkGrey"></div>
          <div className="p-8 text-white">
            <nav className="flex flex-col gap-4">
              <a href="https://www.arborchat.ai/about-us" target="_blank" rel="noopener noreferrer" className="text-sm">
                About Us
              </a>
              <a href="https://t.me/+2SQuvUeWdnNiNDM9" target="_blank" rel="noopener noreferrer" className="text-sm">
                Join Community
              </a>
              <div
                className={`text-sm text-left cursor-default ${!isDebug ? "opacity-0" : ""}`}
                onClick={() => {
                  setDebug(!isDebug);
                  setShowMenu(false);
                }}
              >
                {isDebug ? "Disable Debug" : "Enable Debug"}
              </div>
            </nav>
          </div>
        </div>
      </div>
      {children}
    </>
  );
};
export default Layout;

