const InstructionCollapse = ({ formula }: { formula: (typeof formulas)[number] }) => {
  const [collapsed, setCollapsed] = useState(true);
  const onCopy = useCallback(() => {
    unsecuredCopyToClipboard(formula.exampleText);
  }, [formula.example]);
  const example = useMemo(() => formula.example(), [formula.name]);

  return (
    <div className="rounded border border-lightGrey p-3 w-full">
      <button
        className="text-sm flex flex-row justify-between items-center w-full cursor-pointer"
        onClick={() => setCollapsed(!collapsed)}
      >
        <span className="font-sm">{formula.name}</span>
        <img src="/assets/dropdown.png" width={16} height={16} alt="arrow" className="inline-block" />
      </button>
      {!collapsed && (
        <>
          <div className="text-xs text-darkGrey my-2">{formula.description}</div>
          <div className="p-3 bg-lightGrey rounded border border-grey mt-2">
            <div className="flex flex-row justify-between items-center">
              <span className="text-xs font-medium">Example</span>
              <button className="text-xs text-darkGrey hover:text-darkGrey/80 cursor-pointer" onClick={onCopy}>
                <img src="/assets/copy.png" width={16} height={16} alt="copy" className="inline-block" />
              </button>
            </div>
            <div className="text-xs mt-1">{example}</div>
          </div>
        </>
      )}
    </div>
  );
};

const Instructions = () => {
  return (
    <div className="p-3">
      <div className="flex items-center flex-col gap-2">
        {formulas.map((formula) => (
          <InstructionCollapse formula={formula} key={formula.name} />
        ))}
      </div>
    </div>
  );
};

export default Instructions;
