import React from "react";
import { UseFormRegister } from "react-hook-form";

interface HostOverrideInputProps {
  register: UseFormRegister<any>;
  id: string;
  label: string;
}

export const HostOverrideInput = ({ register, id, label }: HostOverrideInputProps) => {
  return (
    <div className="">
      <label htmlFor={id} className="items-center font-title text-sm mb-2">
        {label}
      </label>
      <input
        className={`placeholder-color-lightGrey w-full rounded border border-lightGrey p-3 pr-14`}
        type="text"
        id={id}
        {...register(id)}
      />
    </div>
  );
};
