import React, { useState } from "react";
import useConsoleStore from "../../stores/console";

const CollapsibleLogObject = ({ arg }: { arg: any }) => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <div className="px-2 inline-flex flex-row gap-2">
      <button onClick={() => setIsOpen(!isOpen)}>{isOpen ? "v" : ">"}</button>
      {isOpen ? <div className="whitespace-pre-wrap">{JSON.stringify(arg, null, 2)}</div> : <div>{typeof arg}</div>}
    </div>
  );
};

const ConsoleView = ({ onClose }: { onClose: () => void }) => {
  const { logs, clear } = useConsoleStore();

  return (
    <div className="bg-black bg-opacity-80 text-white p-4">
      <div className="flex justify-between items-center mb-2">
        <h3 className="text-sm font-semibold">Console</h3>
        <div>{process.env.REACT_APP_VERSION}</div>
        <button onClick={clear} className="text-xs px-2 py-1 bg-gray-700 rounded hover:bg-gray-600">
          Clear
        </button>
        <button onClick={onClose} className="text-xs px-2 py-1 bg-gray-700 rounded hover:bg-gray-600">
          Close
        </button>
      </div>
      <div className="space-y-1 text-xs font-mono max-h-[400px] overflow-y-auto">
        {logs.map((log, index) => (
          <div key={index} className="bg-red-500">
            <div className="px-2 inline-block">{log.type === "log" ? "[Log] " : "[Err] "}</div>
            {log.args.map((arg, index) =>
              typeof arg === "object" ? (
                <CollapsibleLogObject key={index} arg={arg} />
              ) : (
                <div className="px-2 inline-block" key={index}>
                  {arg}
                </div>
              )
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default ConsoleView;
