import React, { FC, useEffect, useState } from "react";
import { ARBOR_CHAT_API } from "../taskpane";
import axios from "axios";
import { Link } from "react-router-dom";

interface QuotaData {
  used: number;
  pending: number;
  totalLimit: number;
  start: number;
  end: number;
}

const QuotaBar: FC<{ apiHost: string; token: string }> = ({ apiHost, token }) => {
  const [quota, setQuota] = useState<QuotaData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [tokenInvalid, setTokenInvalid] = useState(false);

  const fetchQuota = async () => {
    try {
      setIsRefreshing(true);
      const quotaUrl = `${apiHost || ARBOR_CHAT_API}/api/v1/quota`;

      const response = await axios.get(quotaUrl, {
        headers: {
          "X-API-KEY": token,
        },
      });

      const data = response.data;

      if (!data.success) {
        setError("API Key is invalid");
        setTokenInvalid(true);
        return;
      }

      setQuota(data.result);
      setTokenInvalid(false);
      setError(null);
    } catch (err) {
      setError("API Key is invalid");
      setTokenInvalid(true);
    } finally {
      setIsRefreshing(false);
    }
  };

  useEffect(() => {
    fetchQuota();
    if (!tokenInvalid) {
      // Refresh quota every 5 minutes
      const interval = setInterval(fetchQuota, 60000 * 5);
      return () => clearInterval(interval);
    }
    return () => {};
  }, [token, tokenInvalid]);

  if (error !== null) {
    return (
      <div className="md:block duration-200 mb-4 whitespace-nowrap rounded-lg p-3 font-title text-sm transition-colors mb-4">
        <div className="flex justify-between items-center">
          <div className="text-red">{error}</div>
          <Link to="/settings" className="text-sm text-purple cursor-pointer">
            Go to settings
            <img src="/assets/purple-rarrow.png" width={16} height={16} alt="arrow" className="inline-block" />
          </Link>
        </div>
      </div>
    );
  }

  if (!quota) {
    return null;
  }

  const usedPercentage = ((quota.used + quota.pending) / quota.totalLimit) * 100;
  const endDate = quota.end ? new Date(quota.end).toLocaleDateString() : null;

  return (
    <div className="p-3">
      <div className="flex justify-between items-center mb-2">
        <div className="flex items-center gap-2">
          <div className="text-sm">{quota.totalLimit - quota.used - quota.pending} tokens left</div>

          <button onClick={fetchQuota} disabled={isRefreshing} className="p-1 rounded hover:bg-lightGrey">
            <img src="/assets/refresh.png" alt="refresh" className={`w-4 h-4 ${isRefreshing ? "animate-spin" : ""}`} />
          </button>
        </div>
        <a
          href="https://www.arborchat.ai/pro"
          target="_blank"
          rel="noopener noreferrer"
          className="text-sm text-purple cursor-pointer"
        >
          Buy More
          <img src="/assets/purple-rarrow.png" width={16} height={16} alt="arrow" className="inline-block" />
        </a>
      </div>
      <div className="w-full bg-lightGrey rounded-full h-1 mb-2">
        <div className="bg-purple h-1 rounded-full" style={{ width: `${Math.min(usedPercentage, 100)}%` }} />
      </div>
      <div className="text-sm">
        Usage: {quota.used + quota.pending}/{quota.totalLimit}
      </div>
      <div className="flex items-center gap-2">{endDate && <div className="text-sm">Expiry date: {endDate}</div>}</div>
    </div>
  );
};

export default QuotaBar;
